















export default {
	data() {
		return {
			list:{}
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中',
			mask: false
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.list = res.data;
				uni.hideLoading();
				
			},
			fail: () => {},
			complete: () => {}
		});

	},
	methods: {
		openinfo(e) {
			var newsid = e.currentTarget.dataset.newsid;
			console.log(e);
			uni.navigateTo({
				url: '../detail/detail?newsid='+newsid,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}

	}
}
